@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(20, 15, 49);
    color: lightcyan;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: white;
}

.coin-search {
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.coin-search h1 {
    margin-bottom: 32px;
    text-align: center;
}

.coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);
    color: lightgray;
}

.coin-input::-webkit-input-placeholder{
    color:lightgray
}

.coin-input:-ms-input-placeholder{
    color:lightgray
}

.coin-input::placeholder{
    color:lightgray
}
.coin-container {
    display: flex;
    justify-content: center;
}

.coin-rows {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom:  1px solid white;
    width: 900px;
}

.coin-info {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin-info h1 {
    font-size: 16px;
    width: 150px;
}

.coin-info img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 110px;
}

.coin-volume {
    width: 200px;
}

.coin-marketCap {
    width: 240px;
}

.coin-percent {
    width: 80px;
}

.red {
    color: red;
}

.green {
    color: greenyellow;
}
